.profile {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  width: 450px;
  overflow: hidden;
  font-size: 30px;
  background-color: rgb(214, 214, 202);
  border-radius: 10px;
  box-shadow: -2px 7px 33px 0 rgba(0, 0, 0, 0.71);
  font-size: 20px;
}

.description {
  padding: 20px 0;
}
.avatar {
  border-radius: 50%;
  background-color: #e5e5e5;
}
.name {
  margin-bottom: 15px;

  font-weight: 700;
  font-size: 26px;
}
.tag {
  color: #7a7b77;
  margin-bottom: 15px;
}
.location {
  color: #7a7b77;
}

/* ul */
.stats {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  padding: 0px;
  list-style: none;
  color: black;
  font-weight: 700;

  background-color: #dfdddd;
  border-top: 1px solid #7a7b77;
}

.item {
  display: flex;
  flex-direction: column;
  width: 135px;
  padding: 25px;
  margin: 8px;
  background-color: rgb(186, 185, 174);
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12);
  border-radius: 0px 0px 4px 4px;
}

.label {
  display: block;
  margin-bottom: 10px;
}
.quantity {
  font-weight: 700;
}
